"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Badge } from "@/components/ui/badge"
import { Checkbox } from "@/components/ui/checkbox"
import { Calendar } from "@/components/ui/calendar"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { format } from "date-fns"

interface AttendanceRecord {
  id: string
  studentId: string
  studentName: string
  class: string
  date: string
  status: "present" | "absent" | "late"
}

interface Student {
  id: string
  name: string
  class: string
}

export default function AttendancePage() {
  const [selectedDate, setSelectedDate] = useState<Date>(new Date())
  const [selectedClass, setSelectedClass] = useState<string>("")
  const [attendanceRecords, setAttendanceRecords] = useState<AttendanceRecord[]>([])

  // Mock data
  const students: Student[] = [
    { id: "1", name: "John Doe", class: "10A" },
    { id: "2", name: "Jane Smith", class: "10A" },
    { id: "3", name: "Mike Johnson", class: "10A" },
    { id: "4", name: "Sarah Wilson", class: "9B" },
    { id: "5", name: "David Brown", class: "9B" },
  ]

  const classes = ["10A", "10B", "9A", "9B", "11A", "11B", "12A", "12B"]

  const filteredStudents = selectedClass 
    ? students.filter(student => student.class === selectedClass)
    : []

  const getAttendanceForStudent = (studentId: string, date: string) => {
    return attendanceRecords.find(
      record => record.studentId === studentId && record.date === date
    )
  }

  const handleAttendanceChange = (studentId: string, status: "present" | "absent" | "late") => {
    const dateStr = format(selectedDate, "yyyy-MM-dd")
    const existingRecord = getAttendanceForStudent(studentId, dateStr)
    const student = students.find(s => s.id === studentId)
    
    if (!student) return

    if (existingRecord) {
      setAttendanceRecords(records =>
        records.map(record =>
          record.id === existingRecord.id
            ? { ...record, status }
            : record
        )
      )
    } else {
      const newRecord: AttendanceRecord = {
        id: Date.now().toString() + studentId,
        studentId,
        studentName: student.name,
        class: student.class,
        date: dateStr,
        status
      }
      setAttendanceRecords(records => [...records, newRecord])
    }
  }

  const handleMarkAllPresent = () => {
    filteredStudents.forEach(student => {
      handleAttendanceChange(student.id, "present")
    })
  }

  const handleMarkAllAbsent = () => {
    filteredStudents.forEach(student => {
      handleAttendanceChange(student.id, "absent")
    })
  }

  const getAttendanceStats = () => {
    const dateStr = format(selectedDate, "yyyy-MM-dd")
    const todayRecords = attendanceRecords.filter(record => record.date === dateStr)
    
    const present = todayRecords.filter(r => r.status === "present").length
    const absent = todayRecords.filter(r => r.status === "absent").length
    const late = todayRecords.filter(r => r.status === "late").length
    const total = filteredStudents.length

    return { present, absent, late, total }
  }

  const stats = getAttendanceStats()

  return (
    <div className="flex-1 space-y-6 p-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Attendance</h1>
          <p className="text-muted-foreground">
            Mark and track student attendance
          </p>
        </div>
      </div>

      {/* Date and Class Selection */}
      <Card>
        <CardHeader>
          <CardTitle>Attendance Marking</CardTitle>
          <CardDescription>
            Select date and class to mark attendance
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex gap-4 items-end">
            <div className="space-y-2">
              <Label>Date</Label>
              <Popover>
                <PopoverTrigger asChild>
                  <Button variant="outline" className="w-[240px] justify-start text-left font-normal">
                    {format(selectedDate, "PPP")}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar
                    mode="single"
                    selected={selectedDate}
                    onSelect={(date) => date && setSelectedDate(date)}
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
            </div>
            <div className="space-y-2">
              <Label>Class</Label>
              <Select value={selectedClass} onValueChange={setSelectedClass}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="Select class" />
                </SelectTrigger>
                <SelectContent>
                  {classes.map((cls) => (
                    <SelectItem key={cls} value={cls}>
                      {cls}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            {selectedClass && (
              <div className="flex gap-2">
                <Button onClick={handleMarkAllPresent} variant="outline" size="sm">
                  Mark All Present
                </Button>
                <Button onClick={handleMarkAllAbsent} variant="outline" size="sm">
                  Mark All Absent
                </Button>
              </div>
            )}
          </div>
        </CardContent>
      </Card>

      {/* Attendance Stats */}
      {selectedClass && (
        <div className="grid gap-4 md:grid-cols-4">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Total Students</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats.total}</div>
            </CardContent>
          </Card>
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Present</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">{stats.present}</div>
            </CardContent>
          </Card>
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Absent</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-red-600">{stats.absent}</div>
            </CardContent>
          </Card>
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Late</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-yellow-600">{stats.late}</div>
            </CardContent>
          </Card>
        </div>
      )}

      {/* Student Attendance List */}
      {selectedClass && (
        <Card>
          <CardHeader>
            <CardTitle>Student Attendance - {selectedClass}</CardTitle>
            <CardDescription>
              {format(selectedDate, "EEEE, MMMM d, yyyy")}
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Student Name</TableHead>
                  <TableHead>Class</TableHead>
                  <TableHead>Present</TableHead>
                  <TableHead>Absent</TableHead>
                  <TableHead>Late</TableHead>
                  <TableHead>Status</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {filteredStudents.map((student) => {
                  const attendance = getAttendanceForStudent(student.id, format(selectedDate, "yyyy-MM-dd"))
                  const status = attendance?.status || "unmarked"
                  
                  return (
                    <TableRow key={student.id}>
                      <TableCell className="font-medium">{student.name}</TableCell>
                      <TableCell>{student.class}</TableCell>
                      <TableCell>
                        <Checkbox
                          checked={status === "present"}
                          onCheckedChange={(checked) => {
                            if (checked) handleAttendanceChange(student.id, "present")
                          }}
                        />
                      </TableCell>
                      <TableCell>
                        <Checkbox
                          checked={status === "absent"}
                          onCheckedChange={(checked) => {
                            if (checked) handleAttendanceChange(student.id, "absent")
                          }}
                        />
                      </TableCell>
                      <TableCell>
                        <Checkbox
                          checked={status === "late"}
                          onCheckedChange={(checked) => {
                            if (checked) handleAttendanceChange(student.id, "late")
                          }}
                        />
                      </TableCell>
                      <TableCell>
                        <Badge 
                          variant={
                            status === "present" ? "default" :
                            status === "absent" ? "destructive" :
                            status === "late" ? "secondary" : "outline"
                          }
                        >
                          {status}
                        </Badge>
                      </TableCell>
                    </TableRow>
                  )
                })}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      )}

      {/* Recent Attendance Records */}
      <Card>
        <CardHeader>
          <CardTitle>Recent Attendance Records</CardTitle>
          <CardDescription>
            Latest attendance entries across all classes
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Date</TableHead>
                <TableHead>Student</TableHead>
                <TableHead>Class</TableHead>
                <TableHead>Status</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {attendanceRecords
                .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime())
                .slice(0, 10)
                .map((record) => (
                  <TableRow key={record.id}>
                    <TableCell>{format(new Date(record.date), "MMM d, yyyy")}</TableCell>
                    <TableCell className="font-medium">{record.studentName}</TableCell>
                    <TableCell>{record.class}</TableCell>
                    <TableCell>
                      <Badge 
                        variant={
                          record.status === "present" ? "default" :
                          record.status === "absent" ? "destructive" : "secondary"
                        }
                      >
                        {record.status}
                      </Badge>
                    </TableCell>
                  </TableRow>
                ))}
            </TableBody>
          </Table>
          {attendanceRecords.length === 0 && (
            <div className="text-center py-8 text-muted-foreground">
              No attendance records found. Start marking attendance to see records here.
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
